import { uniFab } from '@dcloudio/uni-ui';
export default {
	components: {
		uniFab
	},
	data() {
		return {
			showEmpty:false,
			sizeCalcState: false,
			tabScrollTop: 0,
			currentId: 1,
			flist: [],
			slist: [],
			tlist: [],
			
			directionStr: '垂直',
			horizontal: 'right',
			vertical: 'bottom',
			direction: 'horizontal',
			pattern: {
				color: '#7A7E83',
				backgroundColor: '#fff',
				selectedColor: '#0b988f',
				buttonColor: '#0b988f'
			},
			content: [
				{
					iconPath: '/static/cart.png',
					selectedIconPath: '/static/cart.png',
					text: '物品篮',
					active: true
				}
			],

			cateList: []
		};
	},
	
	onLoad() {
		this.loadData();
	},
	
	methods: {
		trigger(e) {
			if (e.index === 0) {
				uni.navigateTo({
					url: '../cart/cart'
				});
			}
		},
		
		successCb(rsp) {
			if (rsp.data.error === 0) {
				this.cateList = rsp.data.msg.commoditycategory;
				
				if(this.cateList.length == 0){
					this.showEmpty = true;
				}
				
				let list = this.cateList;
				list.forEach(item => {
					if (item.parent === '-') {
						this.flist.push(item); //pid为父级id, 没有pid或者pid=0是一级分类
					} else if (item.image === '-') {
						this.slist.push(item); //没有图的是2级分类
					} else {
						item.image = getApp().globalData.domain + item.image;
						this.tlist.push(item); //3级分类
					}
				});
			}
		},
		failCb(err) {
			console.log('api_category failed', err);
		},
		completeCb(rsp) {},
		
		loadData() {
			// let list = await this.$api.json('cateList');
			
			this.requestWithMethod(
				getApp().globalData.api_category,
				'GET',
				'',
				this.successCb,
				this.failCb,
				this.completeCb
			);
		},
		//一级分类点击
		tabtap(item) {
			if (!this.sizeCalcState) {
				this.calcSize();
			}

			this.currentId = item.id;
			let index = this.slist.findIndex(sitem => sitem.parent === item.id);
			this.tabScrollTop = this.slist[index].top;
		},
		//右侧栏滚动
		asideScroll(e) {
			if (!this.sizeCalcState) {
				this.calcSize();
			}
			let scrollTop = e.detail.scrollTop;
			let tabs = this.slist.filter(item => item.top <= scrollTop).reverse();
			if (tabs.length > 0) {
				this.currentId = tabs[0].parent;
			}
		},
		//计算右侧栏每个tab的高度等信息
		calcSize() {
			let h = 0;
			this.slist.forEach(item => {
				let view = uni.createSelectorQuery().select('#main-' + item.id);
				view.fields(
					{
						size: true
					},
					data => {
						item.top = h;
						h += data.height;
						item.bottom = h;
					}
				).exec();
			});
			this.sizeCalcState = true;
		},
		navToList(sid, tid, tname) {
			uni.navigateTo({
				url: `/pages/category/category_detail_list?fid=${this.currentId}&sid=${sid}&tid=${tid}&tname=${tname}`
			});
		}
	}
};
